.home-container {
    text-align: center;
    padding: 20px;
  }
  
  .home-banner {
    background-color: #f5f5f5;
    padding: 50px 20px;
    display: flex;
    flex-direction: row;
  }
  
  .home-title {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .home-subtitle {
    font-size: 1.5rem;
    margin-bottom: 40px;
  }
  
  .home-content {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .home-video {
    margin-top: 40px;
    margin-bottom: 40px;
  }
  
  .testimonial-container {
    margin-top: 40px;
  }
  
  .testimonial {
    font-style: italic;
    margin-bottom: 20px;
  }
  
  .recycling-benefits {
    margin-top: 40px;
    padding: 20px;
    background-color: #e0e0e0;
    border-radius: 8px;
  }
  
  .recycling-title {
    font-size: 1.8rem;
    margin-bottom: 20px;
  }
  
  .recycling-list {
    list-style: none;
    padding: 0;
  }
  
  .recycling-list li {
    margin-bottom: 10px;
  }

  .hero-image {
    width: 500;
    height: 500;
    clip-path: xywh(0 5px 100% 90% round 15% 0);
  }

  .working{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }